import { proxy, useSnapshot } from 'valtio';

interface AuthState {
  isLoginDialogOpen: boolean;
  resolver: ((success: boolean) => void) | null;
}

// 使用 valtio 创建一个简单的响应式状态
const state = proxy<AuthState>({
  isLoginDialogOpen: false,
  resolver: null,
});

/**
 * 显示全局登录对话框，并返回一个 Promise。
 * @returns Promise<boolean> - 用户登录成功时 resolve(true)，用户手动关闭时 resolve(false)。
 */
function showLoginDialog(): Promise<boolean> {
  return new Promise((resolve) => {
    state.isLoginDialogOpen = true;
    state.resolver = (success: boolean) => {
      state.isLoginDialogOpen = false;
      state.resolver = null;
      resolve(success);
    };
  });
}

/**
 * 在 LoginDialog 组件中调用，用于处理关闭事件。
 * @param success - 指示登录是否成功。
 */
function onLoginDialogClose(success: boolean) {
  if (state.resolver) {
    state.resolver(success);
  }
}

export const authService = {
  showLoginDialog,
  onLoginDialogClose,
  state, // 导出 state 以便在 _app.tsx 中使用
};

// 这是一个自定义 hook，方便在 React 组件中使用 authService 的状态
export function useAuthService() {
  const snapshot = useSnapshot(authService.state);
  return snapshot;
}
